import styled from 'styled-components';
import JSDev from '../../images/JSDev.jpg';
import BatDev from '../../images/BatDev.jpg';


export const WorkItem = styled.li`
  color: white;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-size: 25px;
  letter-spacing: 2px;
  font-weight: 400;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
`;
export const Experience = styled.div`
position:relative;
top:15px;
display: inline-block;
`;

export const CenterDiv = styled.div`
display: flex;
align-items: center;
justify-content: center;
`;

export const Div = styled.div`
position:relative;
display: inline-block;
white-space: nowrap;
color: white;
margin-top: 0rem;
padding-bottom: 1rem;
padding-top: 1rem;
padding-left:0.5rem;
border-radius: 16px 16px 16px 16px;
`;


export const WorkTitle = styled.h4`
position: relative;
top:15px;
text-align:center;
font-size: 35px;
font-weight: 400;
font-style: normal;
font-variant: normal;
text-transform: none;
color: black;
font-family:'wire one', serif;
font-weight:normal;
`;

export const JobTitle = styled.p`
  font-weight: bold;
  display: inline-block;
  color: black;
`;


export const Unscrollable = styled.div`
align-items: center;
justify-align: center;
color: white;
width: 100%;
height: 100%;
font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
font-size: 25px;
font-weight: 400;
font-style: normal;
font-variant: normal;
text-transform: none;

padding-bottom: 1rem;
padding-left:0.5rem;
`;


export const WorkDiv = styled.div`
position:relative;
background-image: linear-gradient(0deg,rgba(255,255,255,1) 23%,rgba(46,204,64,1) 50%,rgba(255,255,255,1) 70%);
flex-direction: column;
overflow: hidden;
width: 100%;
margin-right: auto;
margin-left: auto;
padding-top:3rem;
`;

export const JsImg = styled.div`
display: inline-block;
vertical-align: middle;
line-height: normal;
white-space: pre-line;
color: white;
background-image: url(${JSDev});
background-repeat: no-repeat;
background-size: contain;
height: 20vh;
width: 100%;
background-position: left;
@media (max-width: 640px) {
  height: 20vhvh;
  background-position: center;
}
`;


export const BackendImg = styled.div`
display: inline-block;
vertical-align: middle;
line-height: normal;
white-space: pre-line;
color: white;
background-image: url(${BatDev});
background-repeat: no-repeat;
background-size: contain;
height: 20vh;
width: 100%;
background-position: center;
@media (max-width: 640px) {
  height: 20vhvh;
  background-position: center;
}
`;





export const WhiteBlock = styled.div`
display: flex;
flex-direction: column;
margin: auto;
text-align: center;
color: white;
font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
font-size: 25px;
font-weight: 400;
font-style: normal;
font-variant: normal;
text-transform: none;
padding-bottom: 1rem;
padding-top: 3rem;
padding-left:0.5rem;
`;