import styled from 'styled-components';
import SpiderWeb from '../../images/SpiderWeb.jpg';

export const EducationItem = styled.li`
color: white;
font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
font-size: 25px;
letter-spacing: 2px;
font-weight: 400;
font-style: normal;
font-variant: normal;
text-transform: none;
padding-bottom: 1rem;
padding-top: 1rem;

`
export const Experience = styled.div`
position:relative;
top:15px;
display: inline-block;
`

export const Institution = styled.h4`
font-weight: bold;
`

export const Degree = styled.p`
top:15px;
text-align:center;
font-size: 30px;
color: black;
font-family:'wire one', serif;
font-weight:normal;
`;

export const ExperienceDiv = styled.div`
background-image: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(46,204,64,1) 50%, rgba(255,255,255,1) 100%);
position: relative;
overflow: hidden;
height: auto;
width: 100%;
padding-right: 15px;
padding-left: 15px;
margin-right: auto;
margin-left: auto;
padding-top: 5rem;
`;


export const WhiteBlockEdu = styled.div`
display: flex;
flex-direction: column;
margin: auto;
text-align: center;
color: white;
font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
font-size: 25px;
font-weight: 400;
font-style: normal;
font-variant: normal;
text-transform: none;
padding-bottom: 1rem;
padding-top: 3rem;
padding-left:0.5rem;
`;

export const WebImg = styled.div`
display: inline-block;
vertical-align: middle;
line-height: normal;
white-space: pre-line;
color: white;
background-image: url(${SpiderWeb});
background-repeat: no-repeat;
background-size: contain;
height: 20vh;
width: 100%;
background-position: right;
@media (max-width: 640px) {
  height: 20vhvh;
  background-position: center;
}
`;